import Vue from 'vue';
import Router from 'vue-router';

import BpHomePage from '../pages/BpHomePage.vue';
import BpResumePage from '../pages/BpResumePage.vue';
import BpEducationPage from '../pages/BpEducationPage.vue';
import BpProjectsPage from '../pages/BpProjectsPage.vue';
import BpSkillsPage from '../pages/BpSkillsPage.vue';

Vue.use(Router);

var routes = [
  {
    path: '/home',
    name: 'home',
    component: BpHomePage
  },
  {
    path: '/resume',
    name: 'resume',
    component: BpResumePage
  },
  {
    path: '/education',
    name: 'education',
    component: BpEducationPage
  },
  {
    path: '/projects',
    name: 'projects',
    component: BpProjectsPage
  },
  {
    path: '/skills',
    name: 'skills',
    component: BpSkillsPage
  },
  {
    path: '*',
    redirect: { name: 'home' },
  },
];

export default new Router({
  routes,
  mode: 'history'
});
